@import url(https://fonts.googleapis.com/css2?family=Sen&family=Space+Mono&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'montserrat', sans-serif;
    background-color: #223343;
    overflow: hidden;
    font-family: 'Space Mono', monospace;
    background-image: url(/exyucovid19/static/media/juga.e0f49827.png);
    background-attachment: fixed;
    background-position: center center;
    background-size: contain;
    background-repeat: no-repeat;
}

.App {
    max-width: 1124px;
    margin: 0 auto;
    position: relative;
    height: 100vh;
    transition: all 0.3s ease;
}

.show,
.hide {
    position: fixed;
    left: 50px;
    top: 50px;
}

.hide {
    top: 100px;
}

h1 {
    color: black;
    padding: 15px;
    background: white;
    font-size: 48px;
    font-weight: 500;
    text-align: center;
    font-family: 'Sen', sans-serif;
}

thead {
    background-color: black;
}

thead tr th {
    color: white !important;
    font-weight: 700 !important;
}

tbody tr:nth-child(even) {
    background-color: #f3f3f3;
}

tbody tr {
    transition: all 0.3s ease-out;
}

tbody tr:hover {
    background-color: gray;
}

tbody tr:hover td {
    color: whitesmoke;
}

@-webkit-keyframes prikazPodataka {
    0% {
        transform: translateY(-1000px);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes prikazPodataka {
    0% {
        transform: translateY(-1000px);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

#prikazPodataka {
    -webkit-animation: prikazPodataka 0.5s forwards;
            animation: prikazPodataka 0.5s forwards;
}
